import {Injectable} from '@angular/core';
import {MapKit} from "../../mapkit";

@Injectable({providedIn: 'root'})
export class AppleMapsGeocoderService {
    private geocoderInstance: MapKit.Geocoder;
    constructor() {
    }

    /**
     * @deprecated use initialize
     */
    public initGeocoder(options: any = {}) {
        this.geocoderInstance = new window.mapkit.Geocoder(options);
    }

    public initialize(options: MapKit.GeocoderConstructorOptions = {}): MapKit.Geocoder {
        this.geocoderInstance = new window.mapkit.Geocoder(options);
        return this.geocoderInstance;
    }

    public reverseLookup(latitude: number, longitude: number, callback: (err: Error, data?: MapKit.GeocoderResponse) => void, options: MapKit.GeocoderLookupOptions = {}) {
        if (!this.geocoderInstance) {
            throw new Error(`Geocoder or API don't initialized`);
        }
        return this.geocoderInstance.reverseLookup(new window.mapkit.Coordinate(latitude, longitude), callback, options);
    }

    public reverseLookupPromised(latitude: number, longitude: number, options: MapKit.GeocoderLookupOptions = {}): Promise<MapKit.GeocoderResponse> {
        if (!this.geocoderInstance) {
            return Promise.reject(`Geocoder or API don't initialized`);
        }
        return new Promise<MapKit.GeocoderResponse>((resolve, reject) => {
            this.geocoderInstance.reverseLookup(new window.mapkit.Coordinate(latitude, longitude), (err, result) => {
                if (err) {
                    reject(err);
                } else {
                    resolve(result);
                }
            }, options);
        });
    }

}
