import {Injectable} from '@angular/core';
import {MapKit} from "../../mapkit";


@Injectable({providedIn: 'root'})
export class AppleMapsSearchService {
    private searchInstance: MapKit.Search;

    constructor() {
    }

    /**
     * @deprecated use initialize
     */
    public initSearch(options: any = {}) {
        this.searchInstance = new window.mapkit.Search(options);
    }

    public initialize(options: MapKit.SearchConstructorOptions = {}): MapKit.Search {
        this.searchInstance = new window.mapkit.Search(options);
        return this.searchInstance;
    }

    public search(query: string, callback: MapKit.SearchCallback, options: MapKit.SearchOptions) {
        if (!this.searchInstance) {
            throw new Error(`Search or API don't initialized`);
        }
        return this.searchInstance.search(query, callback, options);
    }

    public autocomplete(query: string, callback: MapKit.AutocompleteSearchCallback, options: MapKit.SearchAutocompleteOptions) {
        if (!this.searchInstance) {
            throw new Error(`Search or API don't initialized`);
        }
        return this.searchInstance.autocomplete(query, callback, options);
    }

    public searchPromised(query: string, options: MapKit.SearchOptions): Promise<MapKit.SearchResponse> {
        if (!this.searchInstance) {
            return Promise.reject(`Search or API don't initialized`);
        }
        return new Promise<MapKit.SearchResponse>((resolve, reject) => {
            this.searchInstance.search(query, (error, data) => {
                if (error) {
                    reject(error);
                } else {
                    resolve(data);
                }
            }, options);
        });
    }

    public autocompletePromised(query: string, options: MapKit.SearchAutocompleteOptions): Promise<MapKit.SearchAutocompleteResponse> {
        if (!this.searchInstance) {
            return Promise.reject(`Search or API don't initialized`);
        }
        return new Promise<MapKit.SearchAutocompleteResponse>((resolve, reject) => {
            this.searchInstance.autocomplete(query, (error, data) => {
                if (error) {
                    reject(error);
                } else {
                    resolve(data);
                }
            }, options);
        })
    }
}
